<template>
    <div>
        <div class="page-header page-header-small">
            <parallax
                class="page-header-image"
                style="background-image: url('img/way1.jfif')"
            >
            </parallax>
            <div class="content-center">
                <div class="container">
                <h1 class="title">미남이의 블로그</h1>
                <div class="text-center"></div>
                </div>
            </div>
        </div>
        <div class="container">
            <div class="row mt-5">
                <div class="col-md-12">
                    <h2 class="d-flex justify-content-center">{{boardData.subject}}</h2>
                </div>
            </div>
            <div class="container">
                <div class="row">
                    <div class="col-md-8">
                        <p class="d-inline text-muted">{{boardData.uid}}</p>
                        <p class="d-inline text-muted ml-2">{{boardData.updateAt}}</p>
                    </div>
                        <!-- <n-button type="primary" class="mt-0 mb-0 mr-2" round @click="BoardUpdate">수정</n-button> -->
                        <div class="col-md-4 d-flex justify-content-end">
                            <BoardUpdate class="d-inline-block" :boardData="boardData"/>
                            <n-button type="primary" class="mt-0 mb-0 d-inline-block" round @click="BoardDelete" v-if="curUid===boardData.uid">삭제</n-button>
                        </div>
                        <!-- <b-button type="primary" class="d-inline-block col-md-2 mt-0 mb-0 btn-primary btn-round" round @click="BoardDelete">삭제</b-button> -->
                    <hr>
                </div>  
            </div>
            <div class="container">
                <div class="row mt-md-3">
                    <div class="col-md-12">
                        <p class="mt-md-2">
                            {{boardData.content}}
                        </p>
                        <br />
                        <br />
                <div class="row">
                    <div class="col-md-6">
                        <!-- <i class="now-ui-icons ui-2_favourite-28 text-danger p-1" roll="button"></i> -->
                        <i id="heart" class="fas fa-heart fa-lg" roll="button" @click="changeLike" v-if="like==='like success'"></i>
                        <i id="heart" class="far fa-heart fa-lg" roll="button" @click="changeLike" v-if="like==='cancle'"></i>
                        <p class="d-inline-block text-muted pr-3 pl-2"> {{boardData.likes}}</p>
                        <p class="d-inline-block text-muted">댓글 {{commentNumber}}</p>
                    </div>
                </div>        
                        <hr>
                    </div>
                </div>
            </div>
            <div class="container">
                <h4 class="text-muted mt-lg-1">댓글</h4>
                <commentList />
                <commentInput/>
            </div>

        </div>
    </div>

</template>

<script>
import axios from 'axios'
const API_URL = 'http://localhost:8080/'
import { sync } from 'vuex-pathify'

import Button from '@/pages/board/Button.vue'
import BoardUpdate from '@/pages/board/BoardUpdate.vue'
import commentList from '@/pages/board/comment/commentList.vue'
import commentInput from '@/pages/board/comment/commentInput.vue'

export default {
    name: 'BoardDetail',
    data() {
        return {
            boardData: [],
            commentNumber: 'commentNumber',
            like: 'cancle',
        }
    },
    methods: {
        getBoardData() {
            const boardno = this.$route.params.boardno
            axios.get(`${API_URL}api/board/boardno/${boardno}/`)
            .then(res=>{
                this.boardData = res.data
            })
            .catch(err => {
                console.log(err)
            })
        },
        BoardDelete() {
            const boardno = this.$route.params.boardno
            axios.delete(`${API_URL}api/board/${boardno}/`)
            .then(res => {
                this.$router.push({name:'index'})
            })
            .catch(err => {
                console.log(err)
            })
        },
        changeLike() {
            const boardno = this.$route.params.boardno
            const uid = this.curUid
            axios.get(`${API_URL}api/board/like/${boardno}/${uid}/`)
            .then(res =>{
                console.log('좋아요')
                console.log(res)
                this.like = res.data
                if (res.data == 'cancle'){
                    this.boardData.likes -= 1
                } else {
                    this.boardData.likes += 1
                }
                this.changeBoardLike()
                
            })
            .catch(err => {
                console.log(err)
            })
        },
        changeBoardLike() {
                    const boardno = this.$route.params.boardno
                    axios.put(`${API_URL}api/board/${boardno}/`, this.boardData.likes)
                    .then(res => {
                        console.log(this.boardData.likes)
                    })
                    .catch(err => {
                        console.log(err)
                    })
                },
        getLike() {
            const boardno = this.$route.params.boardno
            const uid = this.curUid
            axios.get(`${API_URL}api/board/like/${boardno}/${uid}/`)
            .then(res => {
                console.log('하트')
                console.log(res)
                this.like = res.data
            })
            .catch(err => {
                console.log(err)
            })
        }
    },
    created() {
        this.getBoardData()
        // this.getLike()
    },
    computed: {
        curUid: sync('curUid'),
    },
    components: {
        BoardUpdate,
        commentList,
        commentInput,
        [Button.name]: Button,
    },

}
</script>

<style>
#heart {
    color:#ff3636;
    cursor:pointer !important;
}

</style>